import type { BallColor } from '../types';

export class ColorManager {
  private static colors: BallColor[] = [
    { fill: '#fff', stroke: '#ccc', hp: 10 },      // Level 1 - White
    { fill: '#ddd', stroke: '#aaa', hp: 20 },      // Level 2 - Light Gray
    { fill: '#bbb', stroke: '#888', hp: 35 },      // Level 3 - Gray
    { fill: '#999', stroke: '#666', hp: 55 },      // Level 4 - Dark Gray
    { fill: '#ff4444', stroke: '#cc0000', hp: 120 },  // Level 5 - Red (Boss)
    { fill: '#ff8800', stroke: '#cc6600', hp: 180 },  // Level 6 - Orange
    { fill: '#ffdd00', stroke: '#ccaa00', hp: 260 },  // Level 7 - Yellow
    { fill: '#88ff00', stroke: '#66cc00', hp: 360 },  // Level 8 - Lime
    { fill: '#00ff88', stroke: '#00cc66', hp: 480 },  // Level 9 - Green
    { fill: '#0088ff', stroke: '#0066cc', hp: 620 },  // Level 10 - Blue (Boss)
    { fill: '#8800ff', stroke: '#6600cc', hp: 780 },  // Level 11 - Purple
    { fill: '#ff0088', stroke: '#cc0066', hp: 960 },  // Level 12 - Pink
    { fill: '#ff6600', stroke: '#cc4400', hp: 1160 }, // Level 13 - Deep Orange
    { fill: '#ffaa00', stroke: '#cc8800', hp: 1380 }, // Level 14 - Gold
    { fill: '#00ffff', stroke: '#00cccc', hp: 1620 }, // Level 15 - Cyan (Boss)
    { fill: '#ff00ff', stroke: '#cc00cc', hp: 1880 }, // Level 16 - Magenta
    { fill: '#66ff66', stroke: '#44cc44', hp: 2160 }, // Level 17 - Light Green
    { fill: '#ff6666', stroke: '#cc4444', hp: 2460 }, // Level 18 - Light Red
    { fill: '#6666ff', stroke: '#4444cc', hp: 2780 }, // Level 19 - Light Blue
    { fill: '#ffff00', stroke: '#cccc00', hp: 3120 }, // Level 20 - Bright Yellow (Boss)
  ];

  static getColorForLevel(level: number): BallColor {
    const index = Math.min(level - 1, this.colors.length - 1);
    return this.colors[index] ?? this.colors[this.colors.length - 1]!;
  }

  static getExpRequired(level: number): number {
    return Math.floor(10 + level * 5);
  }

  static isBossLevel(level: number): boolean {
    return level === 100;
  }

  static getBossHp(level: number): number {
    return Math.floor(5000 * Math.pow(1.5, (level / 5) - 1));
  }
}

